.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    padding: 20px;
}

.dialogsItems {
    padding: 10px 0;
}

.messages {
    padding: 10px;
}

.textarea {
    width: 100%;
    border-radius: 5px;
    border: 0;
    box-shadow: 0 0 10px rgba(54, 60, 66, 0.1);
    outline: 0;
    font-family: 'Mulish', sans-serif;
    box-sizing: border-box;
    padding: 15px 20px;
    color: #363c42;
    margin-bottom: 20px;
}

.button {
    display: inline-block;
    background-color: rgba(126,206,252,0.3);
    color: #2a2e33;
    padding: 7px 21px;
    font-size: 14px;
    line-height: 22px;
    border-radius: 21px;
    white-space: nowrap;
    cursor: pointer;
    text-shadow: none;
    transition: all 0.25s ease;
    font-family: 'Mulish', sans-serif;
    border: 0;
}

.button:hover {
    background-color: #54595e;
    color: #fff;
}