.messageWrapper {
    margin-bottom: 10px;
}

.message {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(54, 60, 66, 0.1);
    background-color: #ffffff;
}

.myMessage {
    text-align: right;
}

.myMessage .message {
    background-color: rgba(126,206,252,0.3);
}